import { Button } from "@/components/ui/button"
import { Check } from "lucide-react"
import Link from "next/link"
import {
    Accordion,
    AccordionContent,
    AccordionItem,
    AccordionTrigger,
} from "@/components/ui/accordion"

export const metadata = {
    title: "Pricing | Powered Pipeline Pro",
    description: "Simple, transparent pricing for MSPs of all sizes.",
}

export default function PricingPage() {
    return (
        <div className="py-20">
            <div className="container">
                <div className="text-center max-w-3xl mx-auto mb-16">
                    <h1 className="text-4xl md:text-5xl font-bold mb-6">Simple, Transparent Pricing</h1>
                    <p className="text-xl text-muted-foreground">
                        Choose the plan that fits your growth stage. Save 20% with annual billing.
                    </p>
                </div>

                {/* Pricing Cards (Reuse logic or component, but explicit here for details) */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-8 max-w-6xl mx-auto mb-24">
                    {/* Starter */}
                    <div className="p-8 rounded-2xl border bg-card flex flex-col">
                        <h3 className="font-semibold text-xl mb-2">Starter</h3>
                        <div className="text-3xl font-bold mb-4">$79<span className="text-base font-normal text-muted-foreground">/user/mo</span></div>
                        <p className="text-muted-foreground text-sm mb-6">Perfect for small MSPs just starting outreach.</p>
                        <ul className="space-y-3 mb-8 flex-1">
                            {['500 verified contacts/mo', 'Basic AI templates', 'Email sequences', 'Standard support'].map((feature) => (
                                <li key={feature} className="flex items-center text-sm">
                                    <Check className="h-4 w-4 text-primary mr-2" />
                                    {feature}
                                </li>
                            ))}
                        </ul>
                        <Link href="/signup">
                            <Button variant="outline" className="w-full">Start Free Trial</Button>
                        </Link>
                    </div>

                    {/* Professional */}
                    <div className="p-8 rounded-2xl border border-primary bg-primary/5 flex flex-col relative">
                        <div className="absolute -top-4 left-1/2 -translate-x-1/2 bg-primary text-primary-foreground px-3 py-1 rounded-full text-xs font-semibold">
                            Most Popular
                        </div>
                        <h3 className="font-semibold text-xl mb-2">Professional</h3>
                        <div className="text-3xl font-bold mb-4">$149<span className="text-base font-normal text-muted-foreground">/user/mo</span></div>
                        <p className="text-muted-foreground text-sm mb-6">For growing teams needing advanced data.</p>
                        <ul className="space-y-3 mb-8 flex-1">
                            {['2,500 verified contacts/mo', 'Advanced AI features', 'Multi-channel outreach', 'CRM integrations', 'Priority support'].map((feature) => (
                                <li key={feature} className="flex items-center text-sm">
                                    <Check className="h-4 w-4 text-primary mr-2" />
                                    {feature}
                                </li>
                            ))}
                        </ul>
                        <Link href="/signup">
                            <Button className="w-full">Start Free Trial</Button>
                        </Link>
                    </div>

                    {/* Enterprise */}
                    <div className="p-8 rounded-2xl border bg-card flex flex-col">
                        <h3 className="font-semibold text-xl mb-2">Enterprise</h3>
                        <div className="text-3xl font-bold mb-4">Custom</div>
                        <p className="text-muted-foreground text-sm mb-6">For large organizations requiring scale.</p>
                        <ul className="space-y-3 mb-8 flex-1">
                            {['Unlimited contacts', 'Custom AI models', 'Dedicated Success Manager', 'API Access', 'White-label options'].map((feature) => (
                                <li key={feature} className="flex items-center text-sm">
                                    <Check className="h-4 w-4 text-primary mr-2" />
                                    {feature}
                                </li>
                            ))}
                        </ul>
                        <Link href="/contact">
                            <Button variant="outline" className="w-full">Talk to Sales</Button>
                        </Link>
                    </div>
                </div>

                {/* FAQ Section */}
                <div className="max-w-3xl mx-auto">
                    <h2 className="text-3xl font-bold mb-8 text-center">Frequently Asked Questions</h2>
                    <Accordion type="single" collapsible className="w-full">
                        <AccordionItem value="item-1">
                            <AccordionTrigger>Do I need a credit card for the free trial?</AccordionTrigger>
                            <AccordionContent>
                                No, you can start your 14-day free trial without a credit card. You&apos;ll only need to enter payment details when you&apos;re ready to upgrade.
                            </AccordionContent>
                        </AccordionItem>
                        <AccordionItem value="item-2">
                            <AccordionTrigger>What happens if I go over my contact limit?</AccordionTrigger>
                            <AccordionContent>
                                We&apos;ll notify you when you&apos;re close to your limit. You can easily upgrade your plan or purchase add-on credits at any time.
                            </AccordionContent>
                        </AccordionItem>
                        <AccordionItem value="item-3">
                            <AccordionTrigger>Can I cancel anytime?</AccordionTrigger>
                            <AccordionContent>
                                Yes, there are no long-term contracts. You can cancel your subscription at any time from your dashboard.
                            </AccordionContent>
                        </AccordionItem>
                        <AccordionItem value="item-4">
                            <AccordionTrigger>Do you integrate with my CRM?</AccordionTrigger>
                            <AccordionContent>
                                We integrate with major CRMs including Salesforce, HubSpot, Zoho, and ConnectWise.
                            </AccordionContent>
                        </AccordionItem>
                    </Accordion>
                </div>
            </div>
        </div>
    )
}
